
@import url(http://fonts.googleapis.com/earlyaccess/notosanstc.css);
@import url("https://fonts.googleapis.com/css?family=Noto+Sans|Roboto");
@import url("https://fonts.googleapis.com/css?family=Roboto");
@-webkit-keyframes fadeIn {
0% {
    opacity: 0;
}
100% {
    opacity: 1;
}
}
@keyframes fadeIn {
0% {
    opacity: 0;
}
100% {
    opacity: 1;
}
}
@-webkit-keyframes fadeInUp {
0% {
    opacity: 0;
    -webkit-transform: translateY(30px);
            transform: translateY(30px);
}
100% {
    opacity: 1;
    -webkit-transform: translateY(0px);
            transform: translateY(0px);
}
}
@keyframes fadeInUp {
0% {
    opacity: 0;
    -webkit-transform: translateY(30px);
            transform: translateY(30px);
}
100% {
    opacity: 1;
    -webkit-transform: translateY(0px);
            transform: translateY(0px);
}
}
@-webkit-keyframes fadeInDown {
0% {
    opacity: 0;
    -webkit-transform: translateY(-30px);
            transform: translateY(-30px);
}
100% {
    opacity: 1;
    -webkit-transform: translateY(0px);
            transform: translateY(0px);
}
}
@keyframes fadeInDown {
0% {
    opacity: 0;
    -webkit-transform: translateY(-30px);
            transform: translateY(-30px);
}
100% {
    opacity: 1;
    -webkit-transform: translateY(0px);
            transform: translateY(0px);
}
}
body {
  background: #FAFAF5;
  text-align: center;
  font-family: 'Roboto', 'Noto Sans TC', sans-serif;
  color: #555;
  font-size: 14px;
  line-height: 30px;
}
a {
  color: #555;
  text-decoration: none;
  transition: 0.5s 0s;
}
a:hover, a:focus {
  color: #555;
  text-decoration: none;
}
li {
  list-style: none;
}
img {
  max-width: 100%;
}
.slider {
  position: relative;
  background: url("https://unsplash.it/200/300") no-repeat center;
  height: 400px;
  margin-top: 65px;
  margin-bottom: 55px;
  background-size: cover;
}
.overlay {
  position: absolute;
  width: 100%;
  height: 100%;
  z-index: 2;
  background: #000;
  opacity: .3;
  left: 0;
  top: 0;
}
.section-header {
  border-bottom: 2px solid #555;
  max-width: 15em;
  letter-spacing: .2em;
  margin: 30px auto;
  margin-bottom: 5px;
}
.section-header h1 {
    font-size: 26px;
}
.breadcrumb {
  background: transparent;
  font-size: 16px;
  padding: 0;
}
.breadcrumb a:hover {
    color: #3CB371;
}
.breadcrumb a:active, .breadcrumb a:focus {
    color: #2E8B57;
}
.line {
  margin: 20px auto;
  border-bottom: #2E8B57 2px solid;
}
#login .panel, #register .panel, #forget-pass .panel {
  width: 100%;
  height: 100%;
  -webkit-animation: fadeInDown 1s ease both;
          animation: fadeInDown 1s ease both;
  box-shadow: 0px 0px 10px gray;
  margin-top: 70px;
  margin-bottom: 20px;
}
#login .panel img, #register .panel img, #forget-pass .panel img {
    width: 200px;
    height: 200px;
    margin-top: 20px;
}
#login .btn, #register .btn, #forget-pass .btn {
  margin: 10px auto;
  font-weight: 500;
  line-height: 30px;
  transition: 0.5s 0s;
}
#login .btn-block, #register .btn-block, #forget-pass .btn-block {
  width: 60%;
  height: 100%;
}
#login .form-group, #register .form-group, #forget-pass .form-group {
  width: 100%;
  height: 100%;
}
#login .form-group label, #register .form-group label, #forget-pass .form-group label {
    font-size: 20px;
}
#login .form-group i, #register .form-group i, #forget-pass .form-group i {
    margin-right: 10px;
    transition: 0.5s 0s;
}
#login .form-group:hover, #login .form-group:focus, #register .form-group:hover, #register .form-group:focus, #forget-pass .form-group:hover, #forget-pass .form-group:focus {
    color: #3CB371;
}
#login .form-control, #register .form-control, #forget-pass .form-control {
  width: 50%;
  height: 100%;
  display: inline;
  font-size: 16px;
  margin: 5px 10px;
  background: transparent;
  border: none;
  border-radius: 0px;
  outline: none;
  box-shadow: none;
  border-bottom: 1px solid gray;
}
#login .form-control:focus, #register .form-control:focus, #forget-pass .form-control:focus {
    border: none;
    outline: none;
    box-shadow: none;
    border-bottom: 2px solid #3CB371;
    color: #000;
}
#app {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
}



